import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger.js";
export default {
    state: {
        timelineItems: [
            {
                title: ' ',
                subtitle: '  , , .  .',
                desc: ': 2 - 4 ',
                animSide: 'slide-left',
            },
            {
                title: ',    ',
                subtitle: '       .     .',
                desc: ': 1 - 2 ',
                animSide: 'slide-right',
            },
            {
                title: '    ',
                subtitle: '    GitHub    Trello.           .',
                desc: ': 2 ',
                animSide: 'slide-left',
            },
            {
                title: '  ',
                subtitle: '    .  .    .',
                desc: ': 2 - 4 ',
                animSide: 'slide-right',
            },
            {
                title: '  ',
                subtitle: ' .       .    .',
                desc: ': 2 - 4 ',
                animSide: 'slide-left',
            },
        ],
        neonTimelineTitle: "<span></span>  <span></span>"
    },
    getters: {
        timelineItems(state) {
            return state.timelineItems;
        },
        neonTimelineTitle(state) {
            return state.neonTimelineTitle;
        },
    },
    actions: {
        timelineSectionAnim() {
            gsap.registerPlugin(ScrollTrigger);

            let timelineItems = gsap.utils.toArray('.timeline__column-item-text')

            gsap.from('.timeline__title', {
                opacity: 0,
                ease: "elastic",
                duration: 1.5,
                y: 30,
                scrollTrigger: {
                    trigger: '.timeline__title',
                    toggleActions: "play pause play pause",
                },
            })

            timelineItems.forEach((item, index) => {
                gsap.from(item, {
                    opacity: 0,
                    ease: "elastic",
                    duration: 1.5,
                    scrollTrigger: {
                        trigger: item,
                        toggleActions: "play pause play pause",
                    },
                    x: () => {
                        if (index % 2 !== 0) {
                            return 100
                        } else {
                            return -100
                        }
                    },
                });
            })

        },
    }
}